.container_all{
    background-color: #e0dede;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}
.container_header{
    background-color: blue;
    width: 100%;
    height:50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    
}
    
.avatar{
    border-radius: 30px;
    background-color: #fff;
    width: 50px;
    height: 50px;
    margin-left: 5px;
}
.container_username{
    font-weight: bold;
    font-size: 15px;
    flex: 1;
    margin-left: 10px;
}
.username{
font-size: 15px;
font-weight: bold;
}
.footer {
    height: 50px;
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.send_container {
    width: 50px;
    height: 40px;
    border-radius: 40px;
    background-color: rgba(5, 5, 255, 0.76);
    margin-left: 10px;
    cursor: pointer;
}
.send{
    justify-content: center;
    padding: 5px;
    align-items: center;
    display: flex;
    font-weight: bold;
}
.input{
    width: 100%;
    height: 100%;
}
.input_container{
flex: 1;
width: 100%;
height: 100%;
}
.chat_container {
    flex: 1;
    background-color: lightgray;
    overflow: auto;
}